	var path = 'G:/GitHub/_private/laoui-bootstrap/doc/pages/components/button/button.html';
	var html = "<div class=\"markdown-body\"><h1 id=\"button-\">Button 按钮</h1>\n<p>按钮用于开始一个即时操作。</p>\n<h2 id=\"-\">何时使用</h2>\n<p>标记了一个（或封装一组）操作命令，响应用户点击行为，触发相应的业务逻辑。</p>\n<h2 id=\"-\">如何使用</h2>\n<ul>\n<li><code>ui-button</code> 指令可以快速创建一个带图标的按钮</li>\n<li>可以使用<code>ui-btn-checkbox</code>属性来控制一组按钮行为类似 checkboxes，或使用 <code>ui-btn-radio</code>属性控制一组按钮行为类似域 radio buttons</li>\n</ul>\n<h2 id=\"-\">属性</h2>\n<h3 id=\"-ui-button-\"><code>ui-button</code> 属性</h3>\n<table>\n<thead>\n<tr>\n<th>成员</th>\n<th>说明</th>\n<th>类型</th>\n<th>默认值</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>type</td>\n<td>按钮类型，可选<code>primary</code>、<code>info</code>、<code>error</code>、<code>default</code>、<code>ghost</code>、<code>dashed</code></td>\n<td>string</td>\n<td>-</td>\n</tr>\n<tr>\n<td>icon</td>\n<td>按钮图标类型</td>\n<td>string</td>\n<td>-</td>\n</tr>\n<tr>\n<td>size</td>\n<td>按钮大小，可选<code>large</code>、<code>small</code>、<code>xsmall</code>、默认是正常大小</td>\n<td>string</td>\n<td>-</td>\n</tr>\n<tr>\n<td>loading</td>\n<td>图标是否旋转，旋转过程按钮disabled状态，还有前提是icon图标是可以旋转</td>\n<td>string</td>\n<td>-</td>\n</tr>\n<tr>\n<td>className</td>\n<td>提供额外样式，类名</td>\n<td>string</td>\n<td>-</td>\n</tr>\n</tbody>\n</table>\n<h3 id=\"-ui-btn-checkbox-\"><code>ui-btn-checkbox</code> 属性</h3>\n<table>\n<thead>\n<tr>\n<th>成员</th>\n<th>默认值</th>\n<th>描述</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>btn-checkbox-false</td>\n<td><code>false</code></td>\n<td>设置值为未选中（unchecked）状态</td>\n</tr>\n<tr>\n<td>btn-checkbox-true</td>\n<td><code>true</code></td>\n<td>设置值为选中（checked）状态</td>\n</tr>\n<tr>\n<td>ng-model</td>\n<td>-</td>\n<td>模型绑定对象</td>\n</tr>\n</tbody>\n</table>\n<h3 id=\"-ui-btn-radio-\"><code>ui-btn-radio</code> 属性</h3>\n<table>\n<thead>\n<tr>\n<th>成员</th>\n<th>默认值</th>\n<th>描述</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>ng-model</td>\n<td>-</td>\n<td>模型绑定对象</td>\n</tr>\n<tr>\n<td>ui-btn-radio</td>\n<td>-</td>\n<td>赋值给<code>ng-model</code>的值</td>\n</tr>\n<tr>\n<td>ui-uncheckable</td>\n<td><code>null</code></td>\n<td>根据给定的值来决定 <code>uncheckable</code> 属性是否存在</td>\n</tr>\n<tr>\n<td>uncheckable</td>\n<td>-</td>\n<td>radio按钮是否可以选中</td>\n</tr>\n</tbody>\n</table>\n<h3 id=\"-uibuttonconfig-\">附加的属性设置 <code>uiButtonConfig</code></h3>\n<table>\n<thead>\n<tr>\n<th>成员</th>\n<th>默认值</th>\n<th>描述</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>activeClass</td>\n<td><code>active</code></td>\n<td>选中按钮样式</td>\n</tr>\n<tr>\n<td>toggleEvent</td>\n<td><code>click</code></td>\n<td>用于切换按钮的事件</td>\n</tr>\n</tbody>\n</table>\n<h2 id=\"-\">代码演示</h2>\n<h3 id=\"-ui-button-\"><code>ui-button</code>举例</h3>\n<div class=\"bs-example\"> \n    <ui-button type=\"primary\" icon=\"download\">下载</ui-button>\n    <ui-button type=\"primary\" icon=\"spinner\" on-click=\"ctrl.click()\" loading=\"ctrl.loading\">点我旋转</ui-button>\n    <ui-button type=\"primary\" icon=\"github\"></ui-button>\n    <ui-button type=\"primary\" icon=\"refresh\" loading=\"true\">刷新中</ui-button>\n    <p>\n    <ui-button type=\"primary\" size=\"large\">超大</ui-button>\n    <ui-button type=\"primary\">正常</ui-button>\n    <ui-button type=\"primary\" size=\"small\">较小</ui-button>\n    <ui-button type=\"primary\" size=\"xsmall\">特小</ui-button>\n    </p>\n</div>\n<ui-clipboard clipboard-target=\"button-example\"></ui-clipboard>\n<div class=\"highlight\" id=\"button-example\">\n    <pre><code style=\"width:100%;color:#fff\">\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span> <span class=\"hljs-attribute\">icon</span>=<span class=\"hljs-string\">&quot;download&quot;</span>&gt;下载&lt;/ui-button&gt;\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span> <span class=\"hljs-attribute\">icon</span>=<span class=\"hljs-string\">&quot;spinner&quot;</span> on-click=<span class=\"hljs-string\">&quot;ctrl.click()&quot;</span> loading=<span class=\"hljs-string\">&quot;ctrl.loading&quot;</span>&gt;点我旋转&lt;/ui-button&gt;\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span> <span class=\"hljs-attribute\">icon</span>=<span class=\"hljs-string\">&quot;github&quot;</span>&gt;&lt;/ui-button&gt;\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span> <span class=\"hljs-attribute\">icon</span>=<span class=\"hljs-string\">&quot;refresh&quot;</span> loading=<span class=\"hljs-string\">&quot;true&quot;</span>&gt;刷新中&lt;/ui-button&gt;\n    &lt;p&gt;\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span> size=<span class=\"hljs-string\">&quot;large&quot;</span>&gt;超大&lt;/ui-button&gt;\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span>&gt;正常&lt;/ui-button&gt;\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span> size=<span class=\"hljs-string\">&quot;small&quot;</span>&gt;较小&lt;/ui-button&gt;\n    &lt;ui-<span class=\"hljs-selector-tag\">button</span> type=<span class=\"hljs-string\">&quot;primary&quot;</span> size=<span class=\"hljs-string\">&quot;xsmall&quot;</span>&gt;特小&lt;/ui-button&gt;\n    &lt;/p&gt;\n    </code></pre>\n</div>\n\n<h3 id=\"-ui-btn-checkbox-ui-btn-radio-\"><code>ui-btn-checkbox</code>和<code>ui-btn-radio</code>举例</h3>\n<div class=\"bs-example\">\n<div>\n    <h4>Single toggle</h4>\n    <pre><code style=\"width:100%;color:#fff\">{{ctrl.singleModel}}</code></pre>\n    <button type=\"button\" class=\"btn btn-primary\" ng-model=\"ctrl.singleModel\" ui-btn-checkbox=\"\" btn-checkbox-true=\"1\" btn-checkbox-false=\"0\">\n        Single Toggle\n    </button>\n    <h4>Checkbox</h4>\n    <pre><code style=\"width:100%;color:#fff\">Model: {{ctrl.checkModel}}</code></pre>\n    <pre><code style=\"width:100%;color:#fff\">Results: {{ctrl.checkResults}}</code></pre>\n    <div class=\"btn-group\">\n        <label class=\"btn btn-primary\" ng-model=\"ctrl.checkModel.left\" ui-btn-checkbox=\"\">Left</label>\n        <label class=\"btn btn-primary\" ng-model=\"ctrl.checkModel.middle\" ui-btn-checkbox=\"\">Middle</label>\n        <label class=\"btn btn-primary\" ng-model=\"ctrl.checkModel.right\" ui-btn-checkbox=\"\">Right</label>\n    </div>\n    <h4>Radio &amp; Uncheckable Radio</h4>\n    <pre><code style=\"width:100%;color:#fff\">{{ctrl.radioModel || <span class=\"hljs-string\">&#39;null&#39;</span>}}</code></pre>\n    <div class=\"btn-group\">\n        <label class=\"btn btn-primary\" ng-model=\"ctrl.radioModel\" ui-btn-radio=\"'Left'\">Left</label>\n        <label class=\"btn btn-primary\" ng-model=\"ctrl.radioModel\" ui-btn-radio=\"'Middle'\">Middle</label>\n        <label class=\"btn btn-primary\" ng-model=\"ctrl.radioModel\" ui-btn-radio=\"'Right'\">Right</label>\n    </div>\n    <div class=\"btn-group\">\n        <label class=\"btn btn-success\" ng-model=\"ctrl.radioModel\" ui-btn-radio=\"'Left'\" uncheckable=\"\">Left</label>\n        <label class=\"btn btn-success\" ng-model=\"ctrl.radioModel\" ui-btn-radio=\"'Middle'\" uncheckable=\"\">Middle</label>\n        <label class=\"btn btn-success\" ng-model=\"ctrl.radioModel\" ui-btn-radio=\"'Right'\" ui-uncheckable=\"ctrl.uncheckable\">Right</label>\n    </div>\n    <div>\n        <button class=\"btn btn-default\" ng-click=\"ctrl.uncheckable = !ctrl.uncheckable\">\n            Toggle uncheckable\n        </button>\n    </div>\n</div>\n</div>\n<ui-clipboard clipboard-target=\"clipboard1\"></ui-clipboard>\n<div class=\"highlight\" id=\"clipboard1\">\n<pre><code style=\"width:100%;color:#fff\"><span class=\"xml\">\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h4</span>&gt;</span>Single toggle<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h4</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">pre</span>&gt;</span></span><span class=\"hljs-template-variable\">{{ctrl.singleModel}}</span><span class=\"xml\"><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">pre</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">&quot;button&quot;</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-primary&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.singleModel&quot;</span> <span class=\"hljs-attr\">ui-btn-checkbox</span>=<span class=\"hljs-string\">&quot;&quot;</span> <span class=\"hljs-attr\">btn-checkbox-true</span>=<span class=\"hljs-string\">&quot;1&quot;</span> <span class=\"hljs-attr\">btn-checkbox-false</span>=<span class=\"hljs-string\">&quot;0&quot;</span>&gt;</span>\n        Single Toggle\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h4</span>&gt;</span>Checkbox<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h4</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">pre</span>&gt;</span>Model: </span><span class=\"hljs-template-variable\">{{ctrl.checkModel}}</span><span class=\"xml\"><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">pre</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">pre</span>&gt;</span>Results: </span><span class=\"hljs-template-variable\">{{ctrl.checkResults}}</span><span class=\"xml\"><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">pre</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn-group&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-primary&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.checkModel.left&quot;</span> <span class=\"hljs-attr\">ui-btn-checkbox</span>=<span class=\"hljs-string\">&quot;&quot;</span>&gt;</span>Left<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-primary&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.checkModel.middle&quot;</span> <span class=\"hljs-attr\">ui-btn-checkbox</span>=<span class=\"hljs-string\">&quot;&quot;</span>&gt;</span>Middle<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-primary&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.checkModel.right&quot;</span> <span class=\"hljs-attr\">ui-btn-checkbox</span>=<span class=\"hljs-string\">&quot;&quot;</span>&gt;</span>Right<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h4</span>&gt;</span>Radio &amp;amp; Uncheckable Radio<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h4</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">pre</span>&gt;</span></span><span class=\"hljs-template-variable\">{{ctrl.radioModel || &#39;null&#39;}}</span><span class=\"xml\"><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">pre</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn-group&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-primary&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.radioModel&quot;</span> <span class=\"hljs-attr\">ui-btn-radio</span>=<span class=\"hljs-string\">&quot;&#39;Left&#39;&quot;</span>&gt;</span>Left<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-primary&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.radioModel&quot;</span> <span class=\"hljs-attr\">ui-btn-radio</span>=<span class=\"hljs-string\">&quot;&#39;Middle&#39;&quot;</span>&gt;</span>Middle<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-primary&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.radioModel&quot;</span> <span class=\"hljs-attr\">ui-btn-radio</span>=<span class=\"hljs-string\">&quot;&#39;Right&#39;&quot;</span>&gt;</span>Right<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn-group&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-success&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.radioModel&quot;</span> <span class=\"hljs-attr\">ui-btn-radio</span>=<span class=\"hljs-string\">&quot;&#39;Left&#39;&quot;</span> <span class=\"hljs-attr\">uncheckable</span>=<span class=\"hljs-string\">&quot;&quot;</span>&gt;</span>Left<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-success&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.radioModel&quot;</span> <span class=\"hljs-attr\">ui-btn-radio</span>=<span class=\"hljs-string\">&quot;&#39;Middle&#39;&quot;</span> <span class=\"hljs-attr\">uncheckable</span>=<span class=\"hljs-string\">&quot;&quot;</span>&gt;</span>Middle<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">label</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-success&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;ctrl.radioModel&quot;</span> <span class=\"hljs-attr\">ui-btn-radio</span>=<span class=\"hljs-string\">&quot;&#39;Right&#39;&quot;</span> <span class=\"hljs-attr\">ui-uncheckable</span>=<span class=\"hljs-string\">&quot;ctrl.uncheckable&quot;</span>&gt;</span>Right<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">label</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;btn btn-default&quot;</span> <span class=\"hljs-attr\">ng-click</span>=<span class=\"hljs-string\">&quot;ctrl.uncheckable = !ctrl.uncheckable&quot;</span>&gt;</span>\n            Toggle uncheckable\n        <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n\n</span></code></pre>\n</div></div>";
