	var path = 'G:/GitHub/_private/laoui-bootstrap/doc/pages/components/modal/modal-form.html';
	var html = "<div class=\"modal-header\">\r\n    <h3 class=\"modal-title\" id=\"modal-title\">I'm a modal!</h3>\r\n</div>\r\n<div class=\"modal-body\" id=\"modal-body\">\r\n    <ul>\r\n        <li ng-repeat=\"item in objFrom.items\">\r\n            <a href=\"#\" ng-click=\"$event.preventDefault(); objFrom.selected.item = item\">{{ item }}</a>\r\n        </li>\r\n    </ul>\r\n    Selected: <b>{{ objFrom.selected.item }}</b>\r\n</div>\r\n<div class=\"modal-footer\">\r\n    <button class=\"btn btn-primary\" type=\"button\" ng-click=\"objFrom.ok()\">保存</button>\r\n    <button class=\"btn btn-warning\" type=\"button\" ng-click=\"objFrom.cancel()\">取消</button>\r\n</div>";
