	var path = 'G:/GitHub/_private/laoui-bootstrap/doc/pages/components/pager/pager.html';
	var html = "<div class=\"markdown-body\"><h1 id=\"pager-\">Pager 分页</h1>\n<p>一个轻量级的上一页/下一页分页指令</p>\n<h3 id=\"ui-pager-\">ui-pager 属性</h3>\n<table>\n<thead>\n<tr>\n<th>成员</th>\n<th>说明</th>\n<th>类型</th>\n<th>默认值</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>align</td>\n<td>每个链接到双方是否对齐。</td>\n<td>boolean</td>\n<td><code>true</code></td>\n</tr>\n<tr>\n<td>items-per-page</td>\n<td>每页记录数。值小于1表示所有记录在一页展示</td>\n<td>number</td>\n<td><code>10</code></td>\n</tr>\n<tr>\n<td>next-text</td>\n<td>下一页按钮文本</td>\n<td>number</td>\n<td><code>Next »</code></td>\n</tr>\n<tr>\n<td>previous-text</td>\n<td>上一页按钮文本</td>\n<td>number</td>\n<td><code>« Previous</code></td>\n</tr>\n<tr>\n<td>ng-disabled</td>\n<td>禁用分页组件</td>\n<td>number</td>\n<td><code>false</code></td>\n</tr>\n<tr>\n<td>num-pages</td>\n<td>一个可选的表达式指定要显示的页面总数</td>\n<td>number</td>\n<td><code>angular.noop</code></td>\n</tr>\n<tr>\n<td>template-url</td>\n<td>自定义模板路径</td>\n<td>string</td>\n<td>-</td>\n</tr>\n<tr>\n<td>total-items</td>\n<td>总记录数</td>\n<td>number</td>\n<td>-</td>\n</tr>\n</tbody>\n</table>\n<h2 id=\"-\">代码演示</h2>\n<div class=\"bs-example\">\n<div>\n  <h4>Pager</h4>\n  <code>You are currently on page {{currentPage}}</code>\n  <ul ui-pager=\"\" total-items=\"totalItems\" ng-model=\"currentPage\"></ul>\n</div>\n</div>\n<ui-clipboard clipboard-target=\"pager-code\"></ui-clipboard>\n<div class=\"highlight\" id=\"pager-code\">\n  <pre><code style=\"width:100%;color:#fff\"><span class=\"xml\">\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h4</span>&gt;</span>Pager<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h4</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">pre</span>&gt;</span>You are currently on page </span><span class=\"hljs-template-variable\">{{currentPage}}</span><span class=\"xml\"><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">pre</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ul</span> <span class=\"hljs-attr\">ui-pager</span>=<span class=\"hljs-string\">&quot;&quot;</span> <span class=\"hljs-attr\">total-items</span>=<span class=\"hljs-string\">&quot;totalItems&quot;</span> <span class=\"hljs-attr\">ng-model</span>=<span class=\"hljs-string\">&quot;currentPage&quot;</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ul</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n  </span></code></pre>\n</div>\n\n<p>controller代码：</p>\n<p><ui-clipboard clipboard-target=\"pager-code\"></ui-clipboard></p>\n<div class=\"highlight\" id=\"pager-code\">\n  <pre><code style=\"width:100%;color:#fff\">\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-type\">ClipboardCtrl</span> from &#39;../../common/clipboard.ctrl.js&#39;;\nexport <span class=\"hljs-keyword\">default</span> <span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">PagerDemoCtrl</span>  <span class=\"hljs-keyword\">extends</span> <span class=\"hljs-title\">ClipboardCtrl</span></span>{\n  constructor($scope, uiNotification) {\n    <span class=\"hljs-string\">&quot;ngInject&quot;</span>;\n    <span class=\"hljs-keyword\">super</span>(uiNotification);\n    $scope.totalItems = <span class=\"hljs-number\">64</span>;\n    $scope.currentPage = <span class=\"hljs-number\">4</span>;\n  }\n}\n  </code></pre>\n</div></div>";
	window.angular.module('ng').run(['$templateCache', function(c) { c.put(path, html) }]);
	module.exports = path;

